import React, { useState, useEffect } from "react";
import Login from "./components/login";
import BoxContainer from "./components/boxContainer";
import ModeSelector from "./components/ModeSelector";
import Table from "react-bootstrap/Table";
import firebase from "./firebase";

function App() {
  const [players, setPlayers] = useState(null);
  const [data, setData] = useState([]);
  const [gameSpec, setGameSpec] = useState({
    mode: false,
    level: false,
    gameId: false,
  });
  const handleLogin = (newPlayer) => {
    firebase
      .firestore()
      .collection("gameStore")
      .add({
        player1: newPlayer.player1,
        player2: newPlayer.player2,
      })
      .then((doc) => {
        setGameSpec((prevState) => ({ ...prevState, gameId: doc.id }));
        setPlayers(newPlayer);
      });
  };
  useEffect(() => {
    firebase
      .firestore()
      .collection("gameStore")
      .onSnapshot((snapshot) => {
        for (let i = 0; i < snapshot.docs.length; i++) {
          data.push(snapshot.docs[i].data());
        }
      });
  }, []);

  return (
    <div className="App">
      {!gameSpec.mode && (
        <ModeSelector
          modeChanger={(id, l1) => setGameSpec({ mode: id, level: l1 })}
        />
      )}
      {players ? (
        <BoxContainer
          players={players}
          mode={gameSpec.mode}
          level={gameSpec.level}
          gameId={gameSpec.gameId}
        />
      ) : (
        gameSpec.mode && (
          <Login
            onLogin={(newPlayer) => handleLogin(newPlayer)}
            mode={gameSpec.mode}
          />
        )
      )}
      <Table>
        <thead>
          <tr>
            <th>PPlayer1</th>
            <th>Player2</th>
            <th>Winner</th>
          </tr>
        </thead>
        <tbody>
          {data.map((obj) => (
            <tr>
              <td>{obj.player1}</td>
              <td>{obj.player2}</td>
              <td>{obj.winner}</td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}

export default App;
